import React, { useState, Fragment } from 'react';
import { Dialog, Transition } from '@headlessui/react';
import { useTranslation } from 'react-i18next';
import { useAuth } from '../context/AuthContext';
import Snackbar from './Snackbar';

interface LoginModalProps {
    isOpen: boolean;
    onClose: () => void;
    onSuccess?: () => void;
}

const LoginModal: React.FC<LoginModalProps> = ({ isOpen, onClose, onSuccess }) => {
    const [userId, setUserId] = useState('');
    const [password, setPassword] = useState('');
    const [showError, setShowError] = useState(false);
    const [rememberMe, setRememberMe] = useState(false);
    const { login } = useAuth();
    const { t } = useTranslation();

    const handleSubmit = (e: React.FormEvent) => {
        e.preventDefault();
        setShowError(false);

        if (login(userId, password)) {
            setUserId('');
            setPassword('');
            onSuccess?.();
            onClose();
        } else {
            setShowError(true);
        }
    };

    const handleGoogleLogin = async () => {
        // Removed Firebase auth import, so this function will not work as expected
        console.log('Google login functionality removed');
    };

    return (
        <Transition appear show={isOpen} as={Fragment}>
            <Dialog as="div" className="relative z-10" onClose={onClose}>
                <div className="fixed inset-0 bg-black/50 backdrop-blur-sm" aria-hidden="true" />
                <div className="fixed inset-0 flex items-start justify-center p-4 pt-16">
                    <Dialog.Panel className="mx-auto max-w-md w-full rounded-2xl bg-white shadow-2xl transform transition-all">
                        <div className="relative">
                            {/* Error Snackbar */}
                            <Snackbar
                                isOpen={showError}
                                message={t('Login Failed')}
                                onClose={() => setShowError(false)}
                                variant="error"
                                className="!fixed !top-24"
                            />
                            {/* Header with decorative gradient */}
                            <div className="absolute inset-0 h-40 bg-gradient-to-br from-blue-500 to-purple-600 rounded-t-2xl" />
                            

                            {/* Login form */}
                            <div className="relative px-8 pt-16 pb-8">
                                {/* Welcome text */}
                                <div className="text-center mb-6">
                                    <h2 className="text-3xl font-bold text-white mb-1">{t('Welcome back')}</h2>
                                    <p className="text-blue-100">{t('Please enter your credentials')}</p>
                                </div>

                                <form onSubmit={handleSubmit} className="space-y-4 pt-2">
                                    <div className="bg-white rounded-xl p-4">
                                        <div className="space-y-4">
                                            {/* User ID input */}
                                            <div>
                                                <label className="block text-sm font-medium text-gray-700 mb-2">
                                                    {t('User ID')}
                                                </label>
                                                <input
                                                    type="text"
                                                    value={userId}
                                                    onChange={(e) => setUserId(e.target.value)}
                                                    className="w-full px-4 py-2.5 border border-gray-200 rounded-lg focus:outline-none focus:border-teal-500 transition-colors bg-white"
                                                    required
                                                />
                                            </div>

                                            {/* Password input */}
                                            <div>
                                                <label className="block text-sm font-medium text-gray-700 mb-2">
                                                    {t('Password')}
                                                </label>
                                                <input
                                                    type="password"
                                                    value={password}
                                                    onChange={(e) => setPassword(e.target.value)}
                                                    className="w-full px-4 py-2.5 border border-gray-200 rounded-lg focus:outline-none focus:border-teal-500 transition-colors bg-white"
                                                    required
                                                />
                                            </div>

                                            {/* Remember me checkbox */}
                                            <div className="flex items-center">
                                                <input
                                                    type="checkbox"
                                                    id="remember-me"
                                                    checked={rememberMe}
                                                    onChange={(e) => setRememberMe(e.target.checked)}
                                                    className="h-4 w-4 text-teal-500 border-gray-300 rounded focus:ring-0"
                                                />
                                                <label htmlFor="remember-me" className="ml-2 block text-sm text-gray-600">
                                                    {t('Remember me')}
                                                </label>
                                            </div>

                                            {/* Error message */}
                                            {showError && (
                                                <div className="text-red-500 text-sm text-center">
                                                    {t('Invalid username or password')}
                                                </div>
                                            )}
                                        </div>
                                    </div>

                                    {/* Action buttons */}
                                    <div className="flex gap-4 justify-between mt-6">
                                        <button
                                            type="button"
                                            onClick={onClose}
                                            className="px-6 py-2.5 text-gray-600 font-medium hover:text-gray-800 focus:outline-none transition-colors"
                                        >
                                            {t('Cancel')}
                                        </button>
                                        <button
                                            type="submit"
                                            className="px-12 py-2.5 bg-gradient-to-r from-emerald-400 via-teal-500 to-cyan-500 text-white rounded-lg text-base font-medium focus:outline-none transform hover:scale-102 transition-all duration-500 hover:shadow-lg"
                                        >
                                            {t('Login')}
                                        </button>
                                    </div>
                                </form>
                            </div>
                        </div>
                        <div className="mt-4">
                            <button
                                type="button"
                                className="inline-flex justify-center rounded-md border border-transparent bg-blue-100 px-4 py-2 text-sm font-medium text-blue-900 hover:bg-blue-200 focus:outline-none focus-visible:ring-2 focus-visible:ring-blue-500 focus-visible:ring-offset-2"
                                onClick={handleGoogleLogin}
                            >
                                {t('login.googleLogin')}
                            </button>
                        </div>
                    </Dialog.Panel>
                </div>
            </Dialog>
        </Transition>
    );
}

export default LoginModal;
